.projects {
    background-color: #E7EEF2;
 }
 
 .projects .row {
    display: flex;
    flex-direction: column;
 }

 .card-container {
    display: flex;
    flex-direction: row;
  }
  
  .card {
    background-color: white;
    flex-basis: 0;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    max-width: 100%;
    margin: 1em;
    border: 1px transparent;
    border-radius: 4px;
  }
  
  .card-image {
    max-height: 200px;
    overflow-y: hidden;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }
  
  .card-image img {
    min-width: 100%; 
    width: 100%; 
    height: auto;
  }
  
  .card-text {
    padding: 1em 2em;
  }

  @media screen and (max-width: 768px) {
    .card-container {
      flex-direction: column;
    }
  }